<template>
  <div>
    <analysis-header @refresh="refresh" @interval="interval"></analysis-header>
    <div id="base">
      <a-row :gutter="[32,16]">
        <a-col :span="2" class="col">
          <city-dropdown @onClick="onClick"></city-dropdown>
        </a-col>
        <a-col :span="22" class="col">
          <div class="chart" id="funnel"></div>
        </a-col>
      </a-row>
      <a-divider></a-divider>
      <a-row :gutter="[32,16]">
        <a-col :span="24" class="col">
          <div class="chart" id="line"></div>
        </a-col>
      </a-row>
      <a-row class="bar-row">
        <a-col :span="2" class="col">
          <city-dropdown @onClick="barOnClick"></city-dropdown>
        </a-col>
        <a-col :span="22" class="col">
          <div class="chart" id="bar"></div>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import {
  getCostFunnel,
  getCostEffectiveLine,
  getCostEffectiveBar
} from "network/analysis";
import AnalysisHeader from "./childComp/AnalysisHeader";
import CityDropdown from "./childComp/CityDropdown";

export default {
  name: "Cost",
  data() {
    return {
      costFunnel: "",
      costEffective: "",
      costEffectiveBar: "",
      isInterval: false
    };
  },
  components: {
    AnalysisHeader,
    CityDropdown
  },
  mounted() {
    this.costFunnel = echarts.init(document.getElementById("funnel"), "white", {
      renderer: "canvas"
    });
    this.costEffective = echarts.init(
      document.getElementById("line"),
      "white",
      {
        renderer: "canvas"
      }
    );
    this.costEffectiveBar = echarts.init(
      document.getElementById("bar"),
      "white",
      {
        renderer: "canvas"
      }
    );
    this.$nextTick(() => {
      this.getEchart();
    });
  },
  methods: {
    getEchart() {
      getCostFunnel().then(res => {
        this.costFunnel.setOption(res.data.data);
      });
      getCostEffectiveLine().then(res => {
        this.costEffective.setOption(res.data.data);
      });
      getCostEffectiveBar().then(res => {
        this.costEffectiveBar.setOption(res.data.data);
      });
    },
    /**
     * 定时刷新
     */
    chartInterval() {
      if (!this.isInterval) {
        return;
      }
      this.getEchart();
      setTimeout(this.chartInterval, 5000);
    },
    /**
     * 刷新与定时器
     */
    refresh() {
      this.getEchart();
    },
    interval(isInterval) {
      this.isInterval = isInterval;
      this.chartInterval();
    },
    /**
     * 选择城市
     */
    onClick(key) {
      this.costFunnel.clear();
      getCostFunnel(key).then(res => {
        this.costFunnel.setOption(res.data.data);
      });
    },
    barOnClick(key) {
      this.costEffectiveBar.clear();
      getCostEffectiveBar(key).then(res => {
        this.costEffectiveBar.setOption(res.data.data);
      });
    }
  }
};
</script>

<style scoped>
#base {
  height: 600px;
  overflow: hidden;
  overflow-y: scroll;
}
.chart {
  left: 10%;
  width: 70%;
  height: 75vh;
}
#line{
  height: 60vh;
}
#bar {
  width: 80%;
  height: 60vh;
}
.bar-row {
  margin-top: 45px;
}
</style>