<template>
  <div class="opt">
    <div id="header">
      <a-button @click="refresh" type="primary" shape="circle" icon="sync"></a-button>
      <a-button @click="interval(true)" class="interval" type="primary" v-if="!isInterval">
        <a-icon type="history" />开启定时刷新
      </a-button>
      <a-button @click="interval(false)" class="interval" v-else>
        <a-icon type="stop" />关闭定时刷新
      </a-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AnalysisHeader",
  data() {
    return {
      isInterval: false
    };
  },
  methods: {
    refresh() {
      this.$emit("refresh");
    },
    interval(isInterval) {
      this.isInterval = isInterval;
      this.$emit("interval", isInterval);
    }
  }
};
</script>

<style scoped>
#header {
  display: inline;
}
.interval {
  margin-left: 20px;
}
.opt {
  margin-bottom: 20px;
}
</style>