import {
  request
} from "./request"

// 测试用
export function testBar() {
  return request({
    url: '/getBar',
    method: 'GET'
  })
}

// 各城市平均租金——柱状图
export function getAvgRentBar() {
  return request({
    url: '/getAvgRentBar',
    method: 'GET'
  })
}

// 标签词云图
export function getTagsCloud(city) {
  return request({
    url: '/getTagsCloud',
    method: 'GET',
    params: {
      city
    }
  })
}

